import React, { Component } from 'react'

export default class Quality extends Component{
render(){
    return (

        <div className="container">
          <div className="position-relative d-flex align-items-center justify-content-center">
            <h1 className="display-1 text-uppercase text-white" style={{WebkitTextStroke: '1px #dee2e6'}}>Quality</h1>
            <h1 className="position-absolute text-uppercase text-primary">Education &amp; Experience</h1>
          </div>
          <div className="row align-items-center">
              <Education></Education>
              <Experience></Experience>
          </div>
        </div>
      );
}
}
